#intro {
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
}
.bg {
    position: absolute;
    top: 10rem;
    right: 8rem;
    z-index: -1;
    object-fit: cover;
    height: 75vh;
}
.introContent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName {
    color: yellow;
}
.introPara {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btn {
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btnImg {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1.5rem;
    width: 1.5 rem;
}

@media screen and (max-width:840px) {
    .bg {
        right: -20vw;
    }
    
}